import axios from "axios";
import SDK_INFO from "@/utils/userSig";
axios.defaults.baseURL = "https://console.tim.qq.com";
axios.defaults.timeout = 3000;
const sdkappid = SDK_INFO.SDKAppID;
const identifier = "admin";
const random = parseInt(Math.random() * 10000);
const usersig = SDK_INFO.getUserSig(identifier);

axios.interceptors.request.use(
    (request) => {
        if (request.url === "/onlineChat/getUser") {
            request.baseURL = "/onlineChat/getUser";
            request.url = "";
        } else if (request.url === "/onlineChat/kickBidder") {
            request.baseURL = "/onlineChat/kickBidder";
            request.url = "";
        }
        return request;
    },
    (error) => {
        throw error;
    }
);

export function post(url, data) {
    return new Promise((resolve, reject) => {
        axios
            .post(
                `${url}?sdkappid=${sdkappid}&identifier=${identifier}&usersig=${usersig}&random=${random}&contenttype=json`,
                data
            )
            .then((res) => {
                resolve(res.data);
            })
            .catch((err) => {
                // reject(err);
                console.log(err)
            });
    });
}
