import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        component: () => import("@/pages/index"),
    },
    {
        path: "/web-chat",
        name: "webChat",
        component: () => import("@/pages/web-chat/web-chat"),
    },
];

const router = new VueRouter({
    routes,
});

export default router;
