import { post } from "./request";

export default {
    // 查询当前用户是否已导入
    seleceUserImport: async (data) => {
        let res = await post("/v4/im_open_login_svc/account_check", data);
        return res;
    },
    // 导入用户（单个导入）
    importUser: async (data) => {
        let res = await post("v4/im_open_login_svc/account_import", data);
        return res;
    },
    // 查询用户在线状态
    isOnline: async (data) => {
        let res = await post("v4/openim/querystate", data);
        return res;
    },
    // 查询已禁言列表
    selectJinyan: async (data) => {
        let res = await post(
            "v4/group_open_http_svc/get_group_shutted_uin",
            data
        );
        return res;
    },
    // 设置群管理
    setGroupAdmin: async (data) => {
        let res = await post(
            "v4/group_open_http_svc/modify_group_member_info",
            data
        );
        return res;
    },
    // 群成员禁言
    memberJinyan: async (data) => {
        let res = await post("v4/group_open_http_svc/forbid_send_msg", data);
        return res;
    },
    // 导出聊天记录
    selectChat: async (data) => {
        let res = await post(
            "v4/group_open_http_svc/group_msg_get_simple",
            data
        );
        return res;
    },
};
