import { Loading } from "element-ui";
import { Notification } from "element-ui";
export async function asyncLoading(callback) {
    const loading = Loading.service({
        lock: true,
        background: "rgba(0, 0, 0, 0.7)",
    });
    try {
        await callback().catch(err=>{
            console.log(err)
        });
    } catch (err) {
        Notification({
            title: "出错了",
            dangerouslyUseHTMLString: true,
            message:
                "<strong>请刷新页面或切换浏览器重试，<strong>建议您使用<span style='color: #418fe8'>谷歌浏览器</span>、<span style='color: #418fe8'>Microsoft Edge</span>、<span style='color: #418fe8'>QQ浏览器</span>或<span style='color: #418fe8'>360浏览器极速模式</span>以获得更好的即时通信体验</strong></strong>",
            type: "error",
            duration: 3000,
        });
        console.log("loading", err);
    } finally {
        loading.close();
    }
}
