import Vue from "vue";
import Vuex from "vuex";
import SDK_INFO from "@/utils/userSig";
// 引入实时音视频
import TRTC from "trtc-js-sdk";
TRTC.Logger.setLogLevel(TRTC.Logger.LogLevel.NONE);
Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        // vue原型链对象
        vueObj: Vue.prototype,
        // 登录用户信息
        userInfo: null,
        // 左侧会话列表是否关闭
        isLeftHide: true,
        // 选中的会话信息
        checkedConversationInfo: null,
        // 群公告
        groupMsg: "",
        // 右侧群成员列表是否关闭
        isRightHide: true,
        // 音视频相关
        trtcInfo: {
            // TRTC
            TRTC,
            // 浏览器是否支持
            browserSupport: false,
            // 当前设备是否有摄像头
            isCamera: false,
            // 当前设备是否有麦克风
            isMicrophone: false,
            // 音视频对象
            trtcClient: null,
            // 音视频本地流
            localStream: null,
            // 当收到音视频时，用于储存房间号和通话类型
            roomIdAndType: null,
            // 展示控件
            isShowComponent: false,
            // 三种状态：已接听-ANSWERED，带接听-WITH_ANSWER，呼叫中-CALLING，默认为空
            type: null,
            subTitle: "",
            // 对方是否有摄像头设备，默认有
            otherPartyCamera: true,
        },
        // 展开导出预览
        showPdf: false,
        // 主题颜色：0--浅色（默认），1--深色
        theme: 0,
        // 外部界面调用即时通讯消息导出功能
        outImportMessage: false,
        // 是否自动点击
        clickJinyanBtn: false,
    },
    mutations: {
        // 修改登录用户信息
        updateUserInfo(state, newValue) {
            state.userInfo = newValue;
        },
        // 修改左侧列表展开状态
        updateIsLeftHide(state, newValue) {
            state.isLeftHide = newValue;
        },
        // 修改选中的会话信息
        updateCheckedConversationInfo(state, newValue) {
            state.checkedConversationInfo = newValue;
        },
        // 修改右侧列表展开状态
        updateIsRightHide(state, newValue) {
            state.isRightHide = newValue;
        },
        // 更新音视频相关内容
        updateTrtc(state, newValue) {
            for (const key in newValue) {
                state.trtcInfo[key] = newValue[key];
            }
        },
        // 更新群公告
        updateGroupMsg(state, newValue) {
            state.groupMsg = newValue;
        },
        // 是否展示PDF
        updateShowPdf(state, newValue) {
            state.showPdf = newValue;
        },
        // 修改主题颜色
        updateTheme(state, newValue) {
            state.theme = newValue;
        },
        // 更新导出消息的状态
        updateImportMessage(state, newValue) {
            state.outImportMessage = newValue;
        },
        // 更新点击一键禁言的状态
        updateClickJinyanBtn(state, newValue) {
            state.clickJinyanBtn = newValue;
        },
    },
    actions: {
        // 创建音视频对象
        createTrtcObj({ state }) {
            state.trtcInfo.trtcClient = state.trtcInfo.TRTC.createClient({
                mode: "rtc",
                sdkAppId: SDK_INFO.SDKAppID,
                userId: state.userInfo.userID,
                userSig: SDK_INFO.getUserSig(state.userInfo.userID),
            });
        },
        // 初始化本地流
        createNative({ state }, type) {
            state.trtcInfo.localStream = state.trtcInfo.TRTC.createStream({
                userId: state.userInfo.userID,
                audio: true,
                video:
                    type === "video" && state.trtcInfo.isCamera ? true : false,
            });
        },
        // 加入房间
        joinRoom({ state }) {
            state.vueObj.$asyncLoading(async () => {
                return new Promise((resolve) => {
                    state.trtcInfo.trtcClient
                        .join({
                            roomId: state.trtcInfo.roomIdAndType.roomId,
                        })
                        .then(() => {
                            state.trtcInfo.localStream.initialize().then(() => {
                                if (
                                    state.trtcInfo.roomIdAndType.type ===
                                    "video"
                                ) {
                                    state.trtcInfo.localStream.play(
                                        "nativeVideo"
                                    );
                                }
                                state.trtcInfo.trtcClient.publish(
                                    state.trtcInfo.localStream
                                );
                                console.log(
                                    "vuex中的日志打印==================================>加入房间成功、本地流初始化成功、发布本地流成功"
                                );
                                resolve("success");
                            });
                        });
                });
            });
        },
    },
    modules: {},
});
